.loginBlock {
  width: 100%;
  min-height: 100vh;
  padding: 0 20px;
  background: #e9e9e9;
  display: flex;
}

.loginBlock .loginContainer {
  padding: 60px;
  margin: auto;
  width: 100%;
  max-width: 520px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: rgb(3, 44, 99);
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
  border-radius: 8px;
}

.loginBlock .loginContainer label {
  color: white;
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}

.loginBlock .loginContainer input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
  border-radius: 4px;
}
.loginBlock .loginContainer span{
  color: yellow;
}

.loginBlock .loginContainer .btnContainer {
  width: 100%;
  padding: 24px 0;
}

.loginBlock .loginContainer .btnContainer p {
  margin: 14px 0 0 0;
  text-align: right;
  color: #fff;
}

.loginBlock .loginContainer .btnContainer p span {
  color: yellow;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-left: 5px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}

.loginBlock .loginContainer .btnContainer p span:hover {
  color: red;
}

.loginBlock .loginContainer .errorMsg {
  color: red;
  font-size: 16px;
}

